.superInput {
    width: 200px;
    padding: 1px 2px;
    margin: 2px 0;
    box-sizing: border-box;
    border: 3px solid #ccc;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    outline: none;
}
.superInput:focus {
    border: 3px solid #555;
}
.errorInput {
}

.error {
    color: red;
}